<template>
  <div class="section">
    <div class="video_player">
      <span class="close">关闭</span>
      <video ref="video"
      controls="controls"
      x5-playsinline
      autoplay
      playsinline
      webkit-playsinline
      src=""></video>
    </div>
    <div class="pages" id="pages" style="visibility: hidden" ref="pages" v-fullpage="opts">
      <Cover/>
      <Navigation/>
    </div>
  </div>
</template>

<script>
import Cover from '@/components/Cover.vue'
import Navigation from '@/components/Navigation.vue'

export default {
  components: {
    Cover,
    Navigation
  },
  props: {
    id: {
      default: 0,
      type: Number
  }},
  data() {
    const self = this;
    return {
      pageId: 0,
      opts: {
        start: self.id,
        dir: 'v',
        duration: 500,
        beforeChange: function (prev, next) {
          self.pageId = next;
          document.getElementById('pages').style.visibility = 'visible';
          if (next == 0 && window._player) {
            _player.play();
          }
        },
        afterChange: function (prev, next) {
          const links = document.querySelectorAll('.links li>div');
          for (let i = 0; i < links.length; i++) {
            if (next == 1) {
              links[i].classList.add('animate');
              links[i].style.webkitAnimationPlayState = "running";
            } else {
              links[i].classList.remove('animate');
              links[i].style.webkitAnimationPlayState = "paused";
            }
          }

          if (next != 0 && window._player) {
            _player.stop();
          }
        }
      }
    }
  }
}
</script>



