import Vue from 'vue'
import Router from 'vue-router'
import Navigation from '@/pages/Navigation'
import Pages from '@/pages/Pages'

Vue.use(Router)

export default new Router({
  routes: [
      {
        path: '/',
        name: 'Navigation',
        component: Navigation,
        props: true
    },
    {
      path: '/pages/:id',
      name: 'Pages',
      component: Pages,
      props: true
    }
  ]
})



