<template>
  <div class="page">
    <div class="logo">
      <img src="../assets/logo.png">
    </div>
    <div class="header">
      <img src="../assets/navigation_text.png">
    </div>
    <div class="links">
      <ul>
        <li v-on:click="gotoPage(0)"><div>
          <img src="../assets/navigation_link4.png">
        </div></li>
        <li v-on:click="gotoPage(2)"><div>
          <img src="../assets/navigation_link1.png">
        </div></li>
        <li v-on:click="gotoPage(3)"><div>
          <img src="../assets/navigation_link2.png">
        </div></li>
        <li v-on:click="gotoPage(4)"><div>
          <img src="../assets/navigation_link3.png">
        </div></li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    gotoPage: function(id) {
      this.$router.push({ name: 'Pages', params: {id: id}})
    }
  }
}
</script>

<style scoped lang="less">
  @keyframes pulse {
    0% { box-shadow:0px 0px 0px 0px rgba(255, 255, 255, 0.5); }
    50% { box-shadow:0px 0px 20px 5px rgba(255, 255, 255, 0.5); }
    100% { box-shadow:0px 0px 0px 0px rgba(255, 255, 255, 0.5); }
  }
  .page {
    height: 100%;
    width: 100%;
    background: url(../assets/navigation_bg.jpg) no-repeat center;
    background-size: cover;
    .header {
      padding: 100px 45px 40px 45px;
      img {
        width: 100%;
      }
    }
    .links {
      padding: 50px 30px;
      padding-top: 0;
      box-sizing: border-box;
      ul {
        font-size: 0;
        >:nth-child(2) {
          >div.animate {
            animation-delay: 0.8s;
          }
        }
        >:nth-child(3) {
          >div.animate {
            animation-delay: 1.3s;
          }
        }
        >:nth-child(4) {
          >div.animate {
            animation-delay: 1.8s;
          }
        }
        li {
          display: inline-block;
          width: 50%;
          margin-bottom: 15px;
          >div {
            margin: 0 auto;
            padding-bottom: 15px;
            text-align: center;
            width: 90%;
            height: 90%;
            background: rgba(0, 0, 0, 0.5);
            border-radius: 3px;
            border: solid 1px #fff;
            >img {
              width: 50%;
              padding-top: 15px;
            }
            &.animate {
              animation:pulse 1s 1;
              animation-delay: 0.3s;
            }
          }
        }
      }
    }
  }
</style>



