<template>
  <div class="page">
    <div class="logo">
      <img src="../assets/logo.png">
    </div>
    <img class="text" src="../assets/page2_text.png">
    <div class="video" @click="play">
      <div class="cover"></div>
      <img src="../assets/play_button.png" class="play_button"></span>
    </div>
    <ul class="info">
      <li><div></div></li>
      <li><div></div></li>
      <li><div></div></li>
      <li><div></div></li>
      <li><div></div></li>
      <li><div></div></li>
    </ul>
    <span class="slide_up"></span>
  </div>
</template>

<script>
export default {
  methods: {
    play: function() {
      const video = document.querySelector('.video_player video');;
      const player = document.querySelector('.video_player');
      player.style['display'] = 'block';
      video.pause();
      video.setAttribute('src', `static/video/a.mp4`);
      video.load();
      video.play();
    }
  }
}
</script>

<style scoped lang="less">
.page {
  background: url(../assets/page2_bg.jpg) no-repeat center;
  background-size: cover;
  padding: 32% 10% 0 10%;
  box-sizing: border-box;
  >.text {
    left: 30px;
    width: 40%;
  }
  >.video {
    margin-top: 10px;
    width: 100%;
    height: 180px;
    left: 0;
    position: relative;
    .cover {
      background: url(../assets/page2_video_cover.jpg) no-repeat center;
      border: solid 1px #fff;
      height: 100%;
      box-shadow: 0 0 3px #fff;
    }
    .play_button {
      top: 35%;
      position: absolute;
      left: 0;
      right: 0;
      margin: 0 auto;
      width: 50px;
    }
  }
  >.info {
    font-size: 0;
    margin: 0 -10px;
    margin-top: 20px;
    >:nth-child(1) {
      div {
        background-image: url(../assets/page2_info1.png);
      }
    }
    >:nth-child(2) {
      div {
        background-image: url(../assets/page2_info2.png);
      }
    }
    >:nth-child(3) {
      div {
        background-image: url(../assets/page2_info3.png);
      }
    }
    >:nth-child(4) {
      div {
        background-image: url(../assets/page2_info4.png);
      }
    }
    >:nth-child(5) {
      div {
        background-image: url(../assets/page2_info5.png);
      }
    }
    >:nth-child(6) {
      div {
        background-image: url(../assets/page2_info6.png);
      }
    }
    li {
      display: inline-block;
      width: 33.33%;
      height: 90px;
      margin-bottom: 20px;
      padding: 0 10px;
      box-sizing: border-box;
      div {
        width: 100%;
        height: 100%;
        border: solid 1px #fff;
        border-radius: 5px;
        background: rgba(255, 255, 255, 0.3) no-repeat center;
        background-size: 100%;
      }
    }
  }
}
</style>



