<template>
  <div class="page">
    <div class="logo">
      <img src="../assets/logo.png">
    </div>
    <img class="text" src="../assets/page5_text.png">
    <span class="slide_up"></span>
    <div class="play_button2" @click="play">
      <img src="../assets/play_button2.png">
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    play: function() {
      const video = document.querySelector('.video_player video');;
      const player = document.querySelector('.video_player');
      player.style['display'] = 'block';
      video.pause();
      video.setAttribute('src', `static/video/c.mp4`);
      video.load();
      video.play();
    }
  }
}
</script>

<style scoped lang="less">
.page {
  background: url(../assets/page5_bg.jpg) no-repeat center;
  background-size: cover;
  padding: 40% 8% 0 8%;
  box-sizing: border-box;
  >.text {
    position: absolute;
    width: 70%;
    bottom: 100px;
  }
  .play_button2 {
    position: absolute;
    left: 20px;
    bottom: 20px;
    img {
      width: 100px;
    }
  }
}
</style>



