<template>
  <div class="section">
    <div class="video_player">
      <span class="close">关闭</span>
      <video ref="video"
      controls="controls"
      x5-playsinline
      autoplay
      playsinline
      webkit-playsinline
      src=""></video>
    </div>
    <span v-on:click="back" class="back">返回</span>
    <div class="pages" id="pages" style="visibility: hidden" ref="pages" v-fullpage="opts">
      <Page1/>
      <Page2/>
      <Page3/>
      <Page4/>
      <Page5/>
      <Page6/>
    </div>
  </div>
</template>

<script>
import Page1 from '@/components/Page1.vue'
import Page2 from '@/components/Page2.vue'
import Page3 from '@/components/Page3.vue'
import Page4 from '@/components/Page4.vue'
import Page5 from '@/components/Page5.vue'
import Page6 from '@/components/Page6.vue'

export default {
  components: {
    Page1,
    Page2,
    Page3,
    Page4,
    Page5,
    Page6
  },
  props: ['id'],
  data() {
    const self = this;
    return {
      pageId: 0,
      opts: {
        start: self.id,
        dir: 'v',
        duration: 500,
        beforeChange: function (prev, next) {
          self.pageId = next;
          document.getElementById('pages').style.visibility = 'visible';
          if (next == 0 && window._player) {
            _player.play();
          }
        },
        afterChange: function (prev, next) {
          if (next != 0 && window._player) {
            _player.stop();
          }
        }
      }
    }
  },
  mounted() {
    const video = this.$refs.video;
    const videoPlayer = document.querySelector('.video_player');
    const videoPlayerCloseButton = document.querySelector('.video_player .close');
    videoPlayerCloseButton.addEventListener('click', () => {
      videoPlayer.style['display'] = 'none';
      video.pause();
    });
  },
  methods: {
    showDetail: function(id, event) {
      this.detailId = id;
      event.preventDefault();
    },
    back: function(id) {
      this.$router.push({ name: 'Navigation', params: {id: 1}})
    }
  }
}
</script>



